/**
 * 암호화 유틸리티
 * 환경 변수에 저장된 암호화된 비밀번호를 복호화하는 기능 제공
 */

export class CryptoUtil {
  // 간단한 XOR 기반 암호화 (실제 프로덕션에서는 더 강력한 암호화 사용)
  private static readonly SECRET_KEY = 'LOWORLD_SECRET_2025';

  /**
   * 문자열 암호화
   */
  static encrypt(text: string): string {
    const key = this.SECRET_KEY;
    let encrypted = '';
    
    for (let i = 0; i < text.length; i++) {
      const charCode = text.charCodeAt(i) ^ key.charCodeAt(i % key.length);
      encrypted += String.fromCharCode(charCode);
    }
    
    return btoa(encrypted); // Base64 인코딩
  }

  /**
   * 문자열 복호화
   */
  static decrypt(encryptedText: string): string {
    try {
      const key = this.SECRET_KEY;
      const decoded = atob(encryptedText); // Base64 디코딩
      let decrypted = '';
      
      for (let i = 0; i < decoded.length; i++) {
        const charCode = decoded.charCodeAt(i) ^ key.charCodeAt(i % key.length);
        decrypted += String.fromCharCode(charCode);
      }
      
      return decrypted;
    } catch (error) {
      console.warn('복호화 실패:', error);
      return '';
    }
  }

  /**
   * 비밀번호 해시 (단방향)
   */
  static hashPassword(password: string): string {
    let hash = 0;
    for (let i = 0; i < password.length; i++) {
      const char = password.charCodeAt(i);
      hash = ((hash << 5) - hash) + char;
      hash = hash & hash; // Convert to 32bit integer
    }
    return Math.abs(hash).toString(16);
  }
}
